<template>
  <div>
    <b-form-group>
      <label for="companyName">Company Name</label>
      <b-form-input
      type="text"
      id="companyName"
      required
      v-model="data.companyName"></b-form-input>
    </b-form-group>

    <b-form-group>
      <label for="jobTitle">Job Title</label>
      <b-form-input
      type="text"
      id="jobTitle"
      required
      v-model="data.jobTitle"></b-form-input>
    </b-form-group>

    <b-form-group>
      <label for="startDate">Start Date</label>
      <b-form-datepicker
      id="startDate"
      required
      v-model="data.startDate"></b-form-datepicker>
    </b-form-group>

    <b-form-group>
      <label for="endDate">End Date</label>
      <b-form-datepicker
      id="endDate"
      required
      v-model="data.endDate"></b-form-datepicker>
    </b-form-group>
  </div>
</template>

<script>
/* eslint-disable */

import Vue from 'vue';
import { FormDatepickerPlugin, FormInputPlugin, FormGroupPlugin } from 'bootstrap-vue';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-vue/dist/bootstrap-vue.css';

Vue.use(FormInputPlugin);
Vue.use(FormGroupPlugin);
Vue.use(FormDatepickerPlugin);

export default {
  name: 'JobForm',
  props: [
    'data'
  ],
}
</script>



